@import "../../../assets/styles/colors.scss";
.hospital-classification {
  .title {
    color: $black-100;
    font-weight: bold;
    padding-bottom: 8px;
    padding-top: 16px;
    display: flex;
  }
  .btns {
    display: flex;
    justify-content: center;
    margin-top: 48px;
    .next {
      border-radius: 8px;
      width: 30%;
      margin-left: 16px;
      background: $base-color;
      border: none;
      padding: 16px;
      color: #fff;
      font-size: 16px;
      cursor: pointer;
      &.disable {
        opacity: 0.5;
        cursor: unset;
      }
    }
    .back {
      border-radius: 8px;
      background: $gray-200;
      border: none;
      padding: 16px;
      width: 20%;
      color: $black-100;
      font-weight: bold;
      cursor: pointer;
    }
  }
}
